* {
  box-sizing: border-box;
}

.console {
  color: #fff;
  flex-direction: column;
  width: 100%;
  height: 100%;
  font-family: Menlo, monospace;
  line-height: 1.5;
  display: flex;
}

body {
  color-scheme: light;
  color: #000;
  background-color: #ddd;
  width: auto;
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.form-line {
  flex-wrap: wrap;
  gap: 0 5px;
  display: flex;
}

.form-line:not(._no-grow) > * {
  flex-grow: 1;
}

input {
  color: #000;
  background-color: #fff;
  border: 2px solid #2c2929;
  border-radius: 5px;
  width: auto;
  margin: 3px 0;
  padding: 5px;
  font-family: monospace;
  font-size: 20px;
}

button {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border-style: none;
  border-radius: 5px;
  width: auto;
  margin: 3px 0;
  padding: 5px 20px;
  font-size: 20px;
  transition: all .1s;
  box-shadow: inset 0 0 5px #000;
}

button:hover:not(:disabled):not(._tab-active) {
  box-shadow: inset 0 0 2px #787878;
}

button._tab-active {
  box-shadow: none;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  position: relative;
}

button._tab-active:after {
  content: "";
  background-color: #0070ff;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

select {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #20201f;
  border-radius: 5px;
  width: auto;
  margin: 3px 0;
  padding: 5px;
  font-size: 20px;
  transition: all .1s;
}

input:disabled, button:disabled, select:disabled {
  opacity: .7;
  color: #292626;
  cursor: not-allowed;
}

.console-box {
  background-color: #fff;
  height: calc(100vh - 40px);
}

div[class|="Tree-label"] {
  width: 100% !important;
  padding-left: 5px !important;
}
